.game{
    margin: 0 auto;
}

.game_header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem;
    font-size: 2rem;
    background-color: white;
}
.game_header_user_name{
    border-bottom: 1px solid rgb(226, 104, 104);
}
.image_avatar_game{
    max-width: 100px;
}


@media screen and (max-width: 441px) {
    .game_header{
        font-size: 1rem;
    }
    .image_avatar_game{
        max-width: 70px;
    }
  }