.chooseName {
  width: 100%;
  height: 100vh;
  max-width: 1250px;
  margin: 0 auto;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding: 2rem;
}

.chooseName_title {
  color: white;
  font-size: 4rem;
}

.chooseName_inputName > input {
  padding: 1.5rem;
  width: 24rem;
  text-align: center;
  outline: none;
  border: none;
  border-radius: 10px;
  font-size: 2rem;
  background-color: rgba(255, 255, 255, 0.1);
  color:white;
}
.chooseName_inputName > input::placeholder {
  color: rgba(255, 255, 255, 0.322);
}
h3 {
  font-size: 2rem;
  color: white;
  font-weight: 300;
}
.chooseName_chooseAvatar {
  width: 50%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
}

.avatar > .MuiSvgIcon-root {
  font-size: 8rem;
}
.avatar > .MuiSvgIcon-root:hover {
  cursor: pointer;
}

.image_avatar {
  width: 100%;
  max-width: 100px;
  transition: transform 0.2s ease;
  padding: 3px;
  margin-bottom: 2rem;
}
.image_avatar:hover {
  cursor: pointer;
  transform: scale(1.2);
  border: 3px solid white;
  border-radius: 100%;
}

.image_avatar_selected {
  width: 100%;
  max-width: 100px;
  transform: scale(1.2);
  border: 3px solid white;
  border-radius: 100%;
  padding: 3px;
  margin-bottom: 2rem;
}

@media screen and (max-width: 441px) {
  .image_avatar,
  .image_avatar_selected {
    max-width: 60px;
  }
}
@media screen and (max-width: 768px) and (min-width:500px){
  .chooseName_chooseAvatar {
    width: 90%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(95px, 1fr));
  }
}
